class RequestManager {
    constructor() {
        this.idMap = new Map()
    }
    /**
     * IDIDmap
     * @param {string} method - 
     * @param {string} url - URL
     * @param {object} params - 
     * @returns {string|boolean} - IDfalse
     */
    generateId(method, url, params) {
        const id = this.generateUniqueId(method, url, params)
        if (this.idMap.has(id)) {
            return false
        }
        this.idMap.set(id, { method, url, params })
        return id
    }

    /**
     * IDmap
     * @param {string} id - ID
     */
    deleteById(id) {
        this.idMap.delete(id)
    }

    /**
     * ID
     * @param {string} method - 
     * @param {string} url - URL
     * @param {object} params - 
     * @returns {string} - ID
     */
    generateUniqueId(method, url, params) {
        const idString = `${method}-${url}-${this.serializeObject(params)}`
        let id = 0;
        for (let i = 0; i < idString.length; i++) {
            id = ((id << 5) - id) + idString.charCodeAt(i)
            id |= 0;
        }
        return id.toString()
    }

    /**
     * 
     * @param {object} obj - 
     * @returns {string} - 
     */
    serializeObject(obj) {
        const keys = Object.keys(obj).sort()
        const serializedObj = {}
        for (let key of keys) {
            const value = obj[key]
            if (value !== null && typeof value === 'object') {
                serializedObj[key] = this.serializeObject(value)
            } else {
                serializedObj[key] = value
            }
        }
        return JSON.stringify(serializedObj)
    }
}

export default RequestManager